import React, { useState } from 'react';
import LoginPanel from './components/login-panel/index.jsx';

import styles from './style.css';

const TAB_NAME = {
  PHONE: 'phone',
  WECHAT: 'wechat',
};

const menuList = {
  phone: '手机号登录',
  wechat: '微信扫码',
};

const App = () => {
  const [activeTab, setActiveTab] = useState(TAB_NAME.PHONE);

  const handleTabItemClick = (key) => {
    setActiveTab(key);
  };

  return (
    <div className={styles.login}>
      <div className={styles.panel}>
        <div className={styles.menu}>
          {Object.keys(menuList).map((key) => {
            return (
              <div
                key={key}
                className={`${styles.tabItem} ${
                  activeTab === key ? styles.activeTabItem : ''
                }`}
                onClick={() => handleTabItemClick(key)}
              >
                {menuList[key]}
              </div>
            );
          })}
        </div>
        <LoginPanel />
      </div>
    </div>
  );
};

export default App;
